"use client";

import { useRef, useState } from "react";
import { useLayoutEffect } from "react";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
import { Twitter, Linkedin, Github } from "lucide-react";

gsap.registerPlugin(ScrollTrigger);

export default function CTAFooter() {
    const sectionRef = useRef(null);
    const [email, setEmail] = useState("");

    useLayoutEffect(() => {
        const ctx = gsap.context(() => {
            gsap.from(".cta-content > *", {
                scrollTrigger: {
                    trigger: sectionRef.current,
                    start: "top 75%",
                },
                y: 40,
                opacity: 0,
                stagger: 0.1,
                duration: 0.8,
                ease: "power3.out",
            });
        }, sectionRef);

        return () => ctx.revert();
    }, []);

    return (
        <section ref={sectionRef} className="relative">

            {/* CTA Section */}
            <div className="py-32 bg-bg-accent relative overflow-hidden">

                {/* Background decoration */}
                <div className="absolute inset-0 animated-gradient opacity-50" />

                <div className="container-candy relative z-10">
                    <div className="cta-content max-w-3xl mx-auto text-center">

                        <h2 className="font-display text-5xl md:text-6xl text-text-dark mb-6">
                            LET'S BUILD SOMETHING
                            <br />
                            <span className="gradient-text">AMAZING</span>
                        </h2>

                        <p className="text-xl text-text-gray mb-12">
                            Ready to transform your business with AI infrastructure?
                        </p>

                        {/* Email Form */}
                        <div className="flex flex-col sm:flex-row gap-4 justify-center items-center mb-8">
                            <input
                                type="email"
                                value={email}
                                onChange={(e) => setEmail(e.target.value)}
                                placeholder="your@email.com"
                                className="w-full sm:w-80 px-6 py-4 bg-white border-2 border-gray-200 rounded-full text-text-dark placeholder:text-text-light focus:outline-none focus:border-neon-blue transition-colors shadow-lg"
                            />
                            <button className="btn-glow px-10 py-4 bg-gradient-to-r from-neon-blue to-cyber-pink text-white rounded-full font-bold whitespace-nowrap">
                                Get Started â†’
                            </button>
                        </div>

                        <p className="text-text-light">
                            Or email us: <a href="mailto:hello@perioxia.tech" className="text-neon-blue hover:underline">hello@perioxia.tech</a>
                        </p>

                    </div>
                </div>
            </div>

            {/* Footer */}
            <footer className="py-12 bg-white border-t border-gray-100">
                <div className="container-candy">
                    <div className="flex flex-col md:flex-row items-center justify-between gap-8">

                        {/* Logo */}
                        <div className="flex items-center gap-3">
                            <span className="font-display text-2xl text-text-dark">PERIOXIA</span>
                            <span className="text-text-light">|</span>
                            <span className="text-text-gray text-sm">Building tomorrow's infrastructure</span>
                        </div>

                        {/* Social Links */}
                        <div className="flex items-center gap-4">
                            {[
                                { icon: Twitter, href: "#" },
                                { icon: Linkedin, href: "#" },
                                { icon: Github, href: "#" },
                            ].map((social, i) => (
                                <a
                                    key={i}
                                    href={social.href}
                                    className="w-10 h-10 rounded-full border-2 border-gray-200 flex items-center justify-center text-text-gray hover:border-neon-blue hover:text-neon-blue hover:scale-110 transition-all"
                                >
                                    <social.icon size={18} />
                                </a>
                            ))}
                        </div>

                        {/* Status */}
                        <div className="flex items-center gap-2">
                            <div className="pulse-dot" />
                            <span className="text-text-light text-sm">All systems online</span>
                        </div>

                    </div>

                    {/* Copyright */}
                    <div className="mt-8 text-center text-text-light text-sm">
                        Â© 2025 Perioxia Technology. All rights reserved.
                    </div>
                </div>
            </footer>

        </section>
    );
}
