"use client";

import { useRef, useLayoutEffect } from "react";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
import MagneticButton from "./ui/MagneticButton";

gsap.registerPlugin(ScrollTrigger);

const projects = [
    {
        title: "VISIBLO",
        subtitle: "AI Visibility Analytics",
        description: "Track your brand's presence across 150+ AI platforms. Know exactly when and how AI mentions you.",
        status: "Launching Q2 2025",
        isFeatured: true,
    },
    {
        title: "PROJECT ATLAS",
        subtitle: "Coming 2025",
        description: "Neural document intelligence for enterprise knowledge management.",
        status: "In Development",
        isFeatured: false,
    },
    {
        title: "PROJECT HORIZON",
        subtitle: "Coming 2025",
        description: "Predictive CRM with autonomous agent workflows.",
        status: "In Development",
        isFeatured: false,
    },
    {
        title: "MORE COMING",
        subtitle: "The Future",
        description: "We're just getting started. Join the waitlist for early access to everything.",
        status: "Stay Tuned",
        isFeatured: false,
    },
];

export default function HorizontalScroll() {
    const sectionRef = useRef<HTMLDivElement>(null);
    const cardsRef = useRef<HTMLDivElement>(null);

    useLayoutEffect(() => {
        const section = sectionRef.current;
        const cards = cardsRef.current;
        if (!section || !cards) return;

        const scrollWidth = cards.scrollWidth - window.innerWidth + 100;

        const ctx = gsap.context(() => {
            // Horizontal scroll animation
            gsap.to(cards, {
                x: -scrollWidth,
                ease: "none",
                scrollTrigger: {
                    trigger: section,
                    start: "top top",
                    end: () => `+=${scrollWidth}`,
                    scrub: 1,
                    pin: true,
                    anticipatePin: 1,
                    invalidateOnRefresh: true,
                },
            });

            // Card entrance animation
            gsap.from(".scroll-card", {
                opacity: 0,
                y: 60,
                stagger: 0.2,
                scrollTrigger: {
                    trigger: section,
                    start: "top 80%",
                    end: "top 20%",
                    scrub: 1,
                },
            });
        }, section);

        return () => ctx.revert();
    }, []);

    return (
        <section ref={sectionRef} className="h-screen overflow-hidden bg-bg-secondary relative">

            {/* Header (fixed during scroll) */}
            <div className="absolute top-12 left-12 z-10">
                <h2 className="font-display text-4xl md:text-5xl text-text-primary mb-2">
                    COMING SOON
                </h2>
                <p className="text-text-muted font-accent text-sm uppercase tracking-wider">
                    Scroll to explore â†’
                </p>
            </div>

            {/* Cards Container */}
            <div
                ref={cardsRef}
                className="flex gap-8 h-full items-center px-12 pt-24"
                style={{ width: "fit-content" }}
            >
                {projects.map((project, i) => (
                    <div
                        key={i}
                        className={`scroll-card flex-shrink-0 h-[500px] rounded-3xl p-10 flex flex-col justify-between transition-all duration-500 ${project.isFeatured
                                ? "w-[550px] bg-gradient-to-br from-electric-cyan/10 via-laser-purple/10 to-plasma-pink/10 border-2 border-electric-cyan/30 glow-cyan"
                                : "w-[400px] bg-bg-elevated/50 border border-text-muted/10 hover:border-text-muted/30"
                            }`}
                    >
                        {/* Top */}
                        <div>
                            <h3 className={`font-display text-3xl md:text-4xl mb-2 ${project.isFeatured ? "gradient-text" : "text-text-primary"}`}>
                                {project.title}
                            </h3>
                            <p className="font-accent text-sm uppercase tracking-wider text-text-muted mb-6">
                                {project.subtitle}
                            </p>
                            <p className="text-text-secondary leading-relaxed">
                                {project.description}
                            </p>
                        </div>

                        {/* Bottom */}
                        <div>
                            {project.isFeatured ? (
                                <MagneticButton className="px-8 py-4 bg-gradient-to-r from-electric-cyan to-laser-purple rounded-full font-bold text-bg-primary">
                                    Join Waitlist â†’
                                </MagneticButton>
                            ) : (
                                <div className="flex items-center gap-2">
                                    <div className="status-dot" style={{ background: "#F59E0B" }} />
                                    <span className="font-accent text-sm text-text-muted uppercase">
                                        {project.status}
                                    </span>
                                </div>
                            )}
                        </div>
                    </div>
                ))}
            </div>

            {/* Background Gradient */}
            <div className="absolute inset-0 pointer-events-none">
                <div className="absolute top-0 right-0 w-1/3 h-full bg-gradient-to-l from-laser-purple/5 to-transparent" />
                <div className="absolute bottom-0 left-0 w-1/2 h-1/2 bg-gradient-to-tr from-electric-cyan/5 to-transparent" />
            </div>

        </section>
    );
}
