"use client";

import { useLayoutEffect, useRef } from "react";
import gsap from "gsap";

export default function Hero() {
  const containerRef = useRef(null);
  const titleRef = useRef<HTMLHeadingElement>(null);

  useLayoutEffect(() => {
    const ctx = gsap.context(() => {
      // Split text animation - words slide up
      const words = gsap.utils.toArray<HTMLElement>(".hero-word");

      gsap.from(words, {
        y: 120,
        opacity: 0,
        duration: 1,
        stagger: 0.08,
        ease: "power4.out",
        delay: 0.3,
      });

      // Tagline fade up
      gsap.from(".hero-tagline", {
        y: 40,
        opacity: 0,
        duration: 0.8,
        delay: 0.9,
        ease: "power3.out",
      });

      // Buttons bounce in
      gsap.from(".hero-btn", {
        scale: 0.8,
        opacity: 0,
        duration: 0.6,
        stagger: 0.1,
        delay: 1.1,
        ease: "back.out(1.7)",
      });

      // Floating badges
      gsap.from(".hero-badge", {
        y: 30,
        opacity: 0,
        duration: 0.6,
        stagger: 0.1,
        delay: 1.4,
        ease: "power3.out",
      });

    }, containerRef);

    return () => ctx.revert();
  }, []);

  return (
    <section
      ref={containerRef}
      className="relative min-h-screen flex items-center justify-center overflow-hidden px-6"
    >
      {/* Animated gradient background */}
      <div className="absolute inset-0 animated-gradient" />

      {/* Floating shapes */}
      <div className="absolute top-[15%] left-[10%] w-40 h-40 rounded-full bg-gradient-to-br from-neon-blue/30 to-future-purple/30 float blur-sm" />
      <div className="absolute bottom-[20%] right-[15%] w-32 h-32 rounded-full bg-gradient-to-br from-cyber-pink/30 to-energy-orange/30 float float-delay-1 blur-sm" />
      <div className="absolute top-[40%] right-[25%] w-24 h-24 rounded-2xl bg-gradient-to-br from-future-purple/20 to-neon-blue/20 float float-delay-2 blur-sm rotate-12" />

      {/* Content */}
      <div className="relative z-10 text-center max-w-5xl">

        {/* Main Headline */}
        <h1
          ref={titleRef}
          className="font-display text-6xl md:text-8xl lg:text-9xl font-bold leading-[0.95] mb-8"
        >
          <span className="hero-word block text-text-dark">BUILDING</span>
          <span className="hero-word block gradient-text">TOMORROW'S</span>
          <span className="hero-word block text-text-dark">INFRASTRUCTURE</span>
          <span className="hero-word block text-text-dark">TODAY</span>
        </h1>

        {/* Tagline */}
        <p className="hero-tagline text-xl md:text-2xl text-text-gray mb-12">
          Smart AI â€¢ Fast CRM â€¢ Powerful Systems
        </p>

        {/* CTAs */}
        <div className="flex gap-6 justify-center flex-wrap mb-16">
          <button className="hero-btn btn-glow px-10 py-5 bg-gradient-to-r from-neon-blue to-future-purple text-white rounded-full font-bold text-lg">
            Explore Projects
          </button>
          <button className="hero-btn px-10 py-5 border-2 border-text-dark text-text-dark rounded-full font-bold text-lg hover:bg-text-dark hover:text-white transition-colors">
            Let's Talk
          </button>
        </div>

        {/* Floating Badges */}
        <div className="flex gap-4 justify-center">
          <div className="hero-badge px-6 py-3 bg-white rounded-full shadow-lg border-2 border-neon-blue float">
            <span className="font-bold text-neon-blue">ðŸ¤– AI</span>
          </div>
          <div className="hero-badge px-6 py-3 bg-white rounded-full shadow-lg border-2 border-future-purple float float-delay-1">
            <span className="font-bold text-future-purple">ðŸ’¼ CRM</span>
          </div>
          <div className="hero-badge px-6 py-3 bg-white rounded-full shadow-lg border-2 border-cyber-pink float float-delay-2">
            <span className="font-bold text-cyber-pink">âš¡ OS</span>
          </div>
        </div>
      </div>

      {/* Scroll indicator */}
      <div className="absolute bottom-10 left-1/2 -translate-x-1/2 animate-bounce">
        <div className="w-6 h-10 border-2 border-text-gray rounded-full flex justify-center pt-2">
          <div className="w-1.5 h-3 bg-neon-blue rounded-full" />
        </div>
      </div>
    </section>
  );
}
